:root {
  --txt-black: #0F2137;
  --txt-black-secondary: #343D48;
  --txt-blur: #244886;
  --grey: #E5ECF4;
  --bg-blue: #1F3E76;
  --bg-blue-secondary: #2F5392;
  --card-shadow: 0px 8px 24px rgba(69, 88, 157, 0.07);
  --bg-gr-prime: linear-gradient(105.18deg, #1E3C72 -2.04%, #2A5298 107.42%);
}

/* dm-sans-regular - latin */

@font-face {
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(""), url("../fonts/dm-sans/dm-sans-v6-latin-regular.woff2") format("woff2"), url("../fonts/dm-sans/dm-sans-v6-latin-regular.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* dm-sans-500 - latin */

@font-face {
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local(""), url("../fonts/dm-sans/dm-sans-v6-latin-500.woff2") format("woff2"), url("../fonts/dm-sans/dm-sans-v6-latin-500.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* dm-sans-700 - latin */

@font-face {
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(""), url("../fonts/dm-sans/dm-sans-v6-latin-700.woff2") format("woff2"), url("../fonts/dm-sans/dm-sans-v6-latin-700.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

:root {
  --content-width: 1250px;
  --gutter: 15px;
  --mob-padding: 20px;
}

.container {
  max-width: var(--content-width);
  padding-left: var(--mob-padding);
  padding-right: var(--mob-padding);
  margin-left: auto;
  margin-right: auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(var(--gutter) * -1);
  margin-right: calc(var(--gutter) * -1);
}

html {
  font-family: "DM Sans";
  line-height: 1.3;
}

body.content-hidden {
  overflow: hidden;
}

img {
  display: block;
  max-width: 100%;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

h1 {
  font-weight: 800;
  font-size: 30px;
}

h2 {
  font-weight: bold;
  font-size: 24px;
}

h3 {
  font-weight: bold;
  font-size: 20px;
}

h4 {
  font-weight: bold;
  font-size: 16px;
}

p {
  font-weight: normal;
  font-size: 16px;
}

.burger {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 25px;
  cursor: pointer;
  color: #fff;
  z-index: 9;
}

.burger_closed .burger__line:nth-child(1) {
  transform: translate(0, 12px) rotate(45deg);
}

.burger_closed .burger__line:nth-child(2) {
  display: none;
}

.burger_closed .burger__line:nth-child(3) {
  transform: translate(0, -12px) rotate(-45deg);
}

.burger__line {
  height: 2px;
  background: currentColor;
  transition: 0.3s;
}

.btn {
  display: inline-block;
  border: 1.5px solid #ffffff;
  color: #fff;
  padding: 13px 30px;
  font-weight: 500;
  font-size: 16px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  border-radius: 50px;
  appearance: none;
}

.btn_fill {
  background-color: #fff;
  color: var(--txt-black);
}

.btn_prime {
  background-color: var(--bg-blue);
  border-color: transparent;
  color: #fff;
}

.accordeon__body {
  overflow: hidden;
  height: 0;
  transition: 0.3s;
}

.menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.menu > .menu-item.menu-item-has-children > a::after {
  content: "";
  display: inline-block;
  width: 6px;
  height: 6px;
  border-left: 1px solid currentColor;
  border-bottom: 1px solid currentColor;
  margin-left: 5px;
  transform: translateY(-1px) rotate(-45deg);
  vertical-align: middle;
}

.menu .menu-item {
  position: relative;
}

.menu .menu-item:hover a {
  background: #fff;
  color: var(--txt-black);
}

.menu .menu-item:hover .sub-menu {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
}

.menu .menu-item > a {
  display: inline-block;
  color: currentColor;
  font-size: 16px;
  font-weight: normal;
  text-decoration: none;
  padding: 15px;
}

.menu .sub-menu {
  position: absolute;
  top: 100%;
  left: 0;
  width: 150px;
  padding: 0;
  background: #fff;
  list-style-type: none;
  opacity: 0;
  visibility: hidden;
  transform: translate(0, -20px);
  transition: 0.3s;
}

.menu .sub-menu > .menu-item:hover > a {
  background: #f1f1f1;
}

.menu .sub-menu > .menu-item:hover .sub-menu {
  display: block;
}

.menu .sub-menu > .menu-item > a {
  width: 100%;
}

.menu .sub-menu .sub-menu {
  display: none;
  position: absolute;
  top: 0;
  left: 100%;
}

.s-title {
  margin: 0;
  font-weight: bold;
  font-size: 36px;
  letter-spacing: -1.5px;
  text-align: center;
  line-height: 1.2;
}

.s-undertitle {
  font-weight: bold;
  font-size: 14px;
  color: var(--txt-blur);
  text-align: center;
  letter-spacing: 2.1px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin: 0;
  text-transform: uppercase;
}

.plan-card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background: var(--bg-blue-secondary);
  color: #fff;
  padding: 70px 40px 50px;
  border-radius: 10px;
  transition: transform 0.3s;
}

.plan-card:hover {
  transform: translateY(-10px) scale(1.02);
}

.plan-card_prime {
  background: #fff;
  color: var(--txt-black-secondary);
}

.plan-card__title {
  font-weight: bold;
  font-size: 22px;
  margin-top: 0;
  margin-bottom: 7px;
}

.plan-card__subtitle {
  font-weight: normal;
  font-size: 16px;
  margin-top: 0;
  margin-bottom: 60px;
}

.plan-card__features {
  margin: 0;
  padding: 0;
  list-style-type: none;
  max-width: 290px;
  margin-bottom: 70px;
}

.plan-card__feature-item {
  position: relative;
  font-size: 16px;
  margin-bottom: 30px;
  padding-left: 36px;
}

.plan-card__feature-item::before {
  content: url(../img/icons/check-ico.svg);
  position: absolute;
  left: 0;
  top: 1px;
}

.plan-card__feature-item_unchecked::before {
  content: url(../img/icons/check-closed-ico.svg);
}

.plan-card__feature-item:last-child {
  margin-bottom: 0;
}

.plan-card__get {
  margin-top: auto;
  align-self: center;
}

.plan-card__badge {
  position: absolute;
  top: 20px;
  left: 40px;
  padding: 3px 8px;
  font-size: 14px;
  font-weight: 700;
  color: #fff;
  letter-spacing: -0.14px;
  border-radius: 3px;
  background: #ef9e48;
}

.plan-card__price {
  position: absolute;
  top: 70px;
  right: 70px;
  text-align: right;
}

.plan-card__price-txt {
  font-weight: normal;
  font-size: 16px;
  margin-top: 0;
  margin-bottom: 0;
}

.plan-card__price-val {
  font-weight: bold;
  font-size: 26px;
  letter-spacing: -0.55px;
  color: #25cb9e;
  margin-top: 0;
  margin-bottom: 0;
}

.side-menu {
  position: sticky;
  top: 100px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 0;
  transition: 0.2s;
  color: #fff;
  z-index: 2;
}

.header_has-bg,
.header_ip {
  background: #f1f1f1;
  color: var(--txt-black);
}

.header_menu-opend {
  color: var(--txt-black);
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo-wrap {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: currentColor;
}

.header__logo path {
  fill: currentColor;
}

.header__logo-text {
  font-weight: bold;
  font-size: 20px;
  margin-left: 10px;
  opacity: 0.8;
}

.header__cta {
  color: currentColor;
  border-color: currentColor;
}

.header__burger {
  color: currentColor;
}

.footer__inner {
  box-sizing: border-box;
  margin-right: var(--gutter);
  margin-left: var(--gutter);
  width: calc(100% * 0.75 - var(--gutter) * 2);
  margin-left: calc(100% * 0.125 + var(--gutter));
  border-top: 3px solid var(--grey);
  padding-top: 60px;
  padding-bottom: 130px;
}

.footer__col {
  box-sizing: border-box;
  margin-right: var(--gutter);
  margin-left: var(--gutter);
  width: calc(100% * 0.25 - var(--gutter) * 2);
  padding-left: 15px;
}

.footer__col-title {
  font-weight: 500;
  font-size: 18px;
  letter-spacing: -0.5px;
  margin-bottom: 25px;
}

.footer__menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.footer__menu .menu-item {
  margin-bottom: 15px;
  font-weight: normal;
  font-size: 14px;
}

.footer__menu .menu-item > a {
  color: inherit;
  text-decoration: none;
  opacity: 0.8;
}

.footer__menu .menu-item > a:hover {
  text-decoration: underline;
  opacity: 1;
}

.promo {
  position: relative;
  padding-top: 180px;
  padding-bottom: 100px;
  min-height: 100vh;
  background-image: url(../img/promo/pattern.svg), var(--bg-gr-prime);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
  border-radius: 0 0 13% 0/0 0 20% 0;
}

.promo__inner {
  max-width: 650px;
}

.promo__content-wrap {
  position: relative;
  z-index: 1;
}

.promo__title {
  font-weight: bold;
  font-size: 85px;
  line-height: 1.2;
  letter-spacing: -2px;
  margin-top: 0;
  margin-bottom: 30px;
}

.promo__subtitle {
  font-weight: 500;
  font-size: 18px;
  max-width: 510px;
  margin-bottom: 38px;
  line-height: 200%;
}

.promo__cta {
  margin-right: 20px;
}

.promo__btn-watch {
  font-weight: 500;
  font-size: 16px;
  color: #fff;
  background: transparent;
  border: none;
  cursor: pointer;
}

.promo__btn-watch:hover::before {
  transform: scale(1.1) rotate(125deg);
}

.promo__btn-watch::before {
